.cabecalho {
    padding: 10px 0;
}
.cabecalho__formulario {
    display: flex;
    justify-content: space-around;
}
.cabecalho__campo {
    padding: 0 5px;
    margin-right: 10px;
    border: none;

}
.cabecalho__botao {
    cursor: pointer;
    background-color: #ccf0e1;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
}