.TarefasLista__div {
    background-color: #f76a8c;
    padding: 10px 0;
    border-radius: 5px;
    margin: 5px;
    display: flex;
    justify-content: space-around;
    align-items: center ;
}
.TarefasLista__paragrafo {
    margin-right: 15px;
}
.TarefasLista__input {
    overflow-y: auto;
    background-color: transparent;
    border: none;
    resize: none;
}
.TarefasLista__input:focus {
    outline: none;
}
.faicons {
    cursor: pointer;
}
